<template>
  <div>
    <el-table
      :data="tableData">
      <el-table-column
        type="index"
        width="50"
      height="50">
      </el-table-column>
      <el-table-column
        label="username"
        width="200">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.username}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="内容"
        width="200">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.content}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="跟帖"
        width="200">
        <template slot-scope="scope">
          <el-tag type="info">{{ scope.row.commentCount}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="时间"
        width="200">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.createTime}}</span>
        </template>
      </el-table-column>

      <el-table-column
        label="操作"
        width="200">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="danger"
            @click="delData(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>
  </div>
</template>

<script>
  import pagination from '@/components/pagination'
  export default {
    name: "list",
    components:{
      pagination
    },
    data() {
      return {
        tableData:[],
        params: {
          pageSize: 5,
          page: 1,
        }
      }
    },
    methods:{
      getdata(){
        this.$axios.post('getinvit',this.params,{withCredentials: true}).then((res)=>{
          this.tableData = res.data
        })
      },
      delData(val){
        this.$confirm('此操作将永久删除该帖子, 是否继续?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(()=>{
          this.$axios.post('delinvit',{id:val}).then((res)=>{
            if(res.code==200){
              this.$message({
                message: '帖子删除成功',
                type: 'success'
              });
              setTimeout(()=>{
                this.$router.go(0)
              },1000)
            }
          })
        }).catch(()=>{
          console.log(res.err)
        })

      },
      pageSizeChange(val) {
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val) {
        this.params.page = val;
        this.getdata()
      },
    },
    created(){
      this.getdata()
    }
  }
</script>
<style>
  .el-table .cell{
    white-space: nowrap;
  }
</style>
<style scoped>

  .img-item{
    width: 100px;
    max-height: 100px;
  }

  .avatar{
    width: 400px;
    height: 200px;
  }
  .left{
    float: left;
    margin-left: 20px;
  }
  .group{
    line-height: 40px;
    margin-left: 0;
  }
  .reason{
    width: 200px;
  }
  .footer{
    background: #eeeeee;
    /*padding: 10px 0;*/
    margin-bottom: 0;
  }
</style>



